import React from 'react';
import { default as MuiStepConnector, StepConnectorProps } from '@mui/material/StepConnector';

const StepConnector = React.forwardRef<HTMLElement, StepConnectorProps>( ( props, ref ) => {
	return (
		<MuiStepConnector
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<StepConnectorProps>;

export default StepConnector;
